.sizes-test-block {
  margin: 0;
  padding: 0;
  width: -webkit-min-content;
  width: -moz-min-content;
  width: min-content;
  position: fixed;
  top: -150;
  left: -150;
  pointer-events: none;
  opacity: 0;
}

#root {
  position: relative;
}

.mainFallBack {
  background: #301b52;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}

@-webkit-keyframes loading {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}

@keyframes loading {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}

.circleFallBack {
  background: #572c9b;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 3px;
  -webkit-animation: loading 5s linear alternate forwards;
          animation: loading 5s linear alternate forwards;
}

::-webkit-scrollbar {
  width: 6px;
  height: 4px;
  background: #2e2e2e;
}

::-webkit-scrollbar-thumb {
  background: #a9a9a9;
  border-radius: 15px;
}

body {
  overflow-y: scroll !important;
  padding: 0 !important;
}

